html {
  --theme-white: #f9f9f9;
  --theme-gray: #5a6177;
  --theme-yellow: #ffbf00;
  --theme-purple: #9a66ff;
  --theme-blue: #4c97ff;
  --theme-magenta: #cf63cf;
  --theme-orange: #ffab1a;
  --theme-cyan: #5bb1d6;
  --theme-green: #58c059;
  --theme-fire: #ff8c19;
  --theme-red: #ff6619;
  --theme-pink: #ff6680;

  --menu-bar-height: 15vh;
}

body {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--theme-white);
}

.GhostDiv {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.GhostSvg {
  position: absolute;
  top: 0;
  left: 0;
}

.App,
.AppMain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  margin: 0;
  border: 0;
  padding: 0;
}

.AppMain {
  z-index: 1;
  background-color: var(--theme-white);
}

.MenuBar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: var(--menu-bar-height);
  background-color: var(--theme-purple);
  margin: 0;
  border: 0;
  padding: 0;
}

.MenuTab {
  display: inline-block;
  margin: 0;
  border: 0;
  padding: 0 1em;
  font-size: 1em;
  height: var(--menu-bar-height);
  background-color: transparent;
  color: var(--theme-white);
}

.MenuTab--active {
  background-color: var(--theme-magenta);
}

.MenuTab--withLeftBorder {
  border-left: 1px solid var(--theme-white);
}

.MenuTab--withRightBorder {
  border-right: 1px solid var(--theme-white);
}

.MenuTab--disabled {
  background: #aaaaaa;
  opacity: 0.5;
}

.MenuTab--suggested {
  animation: 0.5s infinite alternate menu-tab-blink;
}

@keyframes menu-tab-blink {
  from {
    box-shadow: none;
    background-color: transparent;
  }

  to {
    box-shadow: 0 0 10vh 1vh white;
    background-color: var(--theme-magenta);
  }
}

.MainSvg {
  position: absolute;
  top: var(--menu-bar-height);
  left: 0;
  width: 100vw;
  height: calc(100vh - var(--menu-bar-height));
  margin: 0;
  border: 0;
  padding: 0;
}

.MainSvg--top {
  top: 0;
  height: calc(100vh);
}

.BlockLiteralEditor {
  position: absolute;
  z-index: 2;
  --width: 80vw;
  --height: 50px;
  width: var(--width);
  height: var(--height);
  top: calc(25vh - var(--height) / 2);
  left: calc(50vw - var(--width) / 2);
  background-color: var(--theme-white);
  border: 1px solid var(--theme-gray);
  border-radius: 5px;
  box-shadow: 0 0 10vh 1vh black;
  margin: 0;
  padding: 0;
}

.BlockLiteralEditor__Input {
  position: absolute;
  top: 0;
  left: 0;
  --padding: 16px;
  padding: var(--padding);
  height: calc(100% - 2 * var(--padding) - 2px);
  width: calc(80% - 2 * var(--padding));
  margin: 0;
  border: none;
  outline: none;
  border-radius: 4px 0 0 4px;
  font-size: 16px;
}

.BlockLiteralEditor__Button {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 20%;
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  border-radius: 0 4px 4px 0;
  font-size: 16px;
  background-color: var(--theme-purple);
  color: var(--theme-white);
}
